import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import {Input, Select, Button, Card} from 'antd';
import logo from './bifrostv.png';
import './App.css';

const Option = Select.Option;

let alias = "", address = "", port = "", id = "", alterId = "", protocol = "", kcpType = "none", path = "", tls = "",
  linkString, linkFinal;

function handleAliasChange(event) {
  console.log(event.target.value);
  alias = event.target.value;
}

function handleAddressChange(event) {
  console.log(event.target.value);
  address = event.target.value;
}

function handlePortChange(event) {
  console.log(event.target.value);
  port = event.target.value;
}

function handleIdChange(event) {
  console.log(event.target.value);
  id = event.target.value;
}

function handleAlterIdChange(event) {
  console.log(event.target.value);
  alterId = event.target.value;
}

function handleProtocolChange(value) {
  console.log(value);
  protocol = value;
}

function handleKcpTypeChange(value) {
  console.log(value);
  kcpType = value;
}

function handlePathChange(event) {
  console.log(event.target.value);
  path = event.target.value;
}

function handleTlsChange(value) {
  console.log(value);
  tls = value;
}

function generateLink() {
  linkString = "{\n" +
    "  \"ps\": \"" + alias + "\",\n" +
    "  \"add\": \"" + address + "\",\n" +
    "  \"port\": \"" + port + "\",\n" +
    "  \"id\": \"" + id + "\",\n" +
    "  \"aid\": \"" + alterId + "\",\n" +
    "  \"net\": \"" + protocol + "\",\n" +
    "  \"type\": \"" + kcpType + "\",\n" +
    "  \"host\": \"" + path + "\",\n" +
    "  \"tls\": \"" + tls + "\"\n" +
    "}";
  linkFinal = "vmess://" + Buffer.from(linkString).toString('base64');
  console.log(linkFinal);
  ReactDOM.render(
    <Card className="App-component-result">
      <p><b>Weigh anchor!</b></p>
      <p>{linkFinal}</p>
    </Card>,
    document.getElementById('link'));
}

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo"/>
          <h1 className="App-title">VMess Link <strike>Agile</strike> Generator</h1>
        </header>
        <p className="App-intro">
          For v2rayN / v2rayNG / BifrostV
        </p>
        <div className="App-component">
          <Input placeholder="Alias" onChange={handleAliasChange} ref="alias"/>
        </div>
        <div className="App-component">
          <Input placeholder="Address" onChange={handleAddressChange}/>
        </div>
        <div className="App-component">
          <Input placeholder="Port" onChange={handlePortChange}/>
        </div>
        <div className="App-component">
          <Input placeholder="ID" onChange={handleIdChange}/>
        </div>
        <div className="App-component">
          <Input placeholder="alterID" onChange={handleAlterIdChange}/>
          <Select placeholder="Protocol" className="App-component" onChange={handleProtocolChange}>
            <Option value="tcp">tcp</Option>
            <Option value="kcp">kcp</Option>
            <Option value="ws">ws</Option>
          </Select>
          <Select placeholder="mKCP Header Type (choose none if you are not using mKCP)" className="App-component"
                  onChange={handleKcpTypeChange}>
            <Option value="none">none</Option>
            <Option value="srtp">srtp</Option>
            <Option value="utp">utp</Option>
            <Option value="wechat-video">wechat-video</Option>
          </Select>
        </div>
        <div className="App-component">
          <Input placeholder="WebSocket Path (optional, only requires in WebSocket)" onChange={handlePathChange}/>
          <Select placeholder="Enable TLS?" className="App-component" onChange={handleTlsChange}>
            <Option value="tls">TLS Enabled</Option>
            <Option value="">TLS Disabled</Option>
          </Select>
        </div>
        <div className="App-component">
          <Button type="primary" icon="link" size="large" onClick={generateLink}>HOP ON THIS SHIP</Button>
        </div>
      </div>
    );
  }
}

export default App;



